body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1440px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

* {
  font-family: 'Poppins', sans-serif;
  font-size: 18px;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

/* colors */

/* Default btn */

.btn {
  text-decoration: none;
  display: inline-block;
  padding: 10px 30px;
  color: #fff;
  font-size: 16px;
  background: #56c7e2;
  border-radius: 22px;
}

.btn:hover,
.btn:focus {
  background: #40c0de;
}

.btn:active {
  background: #2ab9db;
}

/* Section Caption */

.section-title {
  font-size: 28px;
  color: #56c7e2;
  text-transform: uppercase;
  font-weight: 600;
}

.section-text {
  display: block;
  font-size: 22px;
  margin-bottom: 25px;
}

/* HEADER */

.header {
  padding: 15px 0;
  box-shadow: 0 6px 4px -4px rgba(0, 0, 0, 0.2);
}

.header .wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.header-list {
  display: flex;
  flex-wrap: wrap;
}

.header-list__item {
  margin: 0 20px;
}

.header-list__item a {
  text-decoration: none;
  font-weight: 300;
  font-size: 18px;
  color: #535d5e;
  text-transform: uppercase;
}

.header-list__item a:hover,
.header-list__item a:focus {
  color: #56c7e2;
}

.header-list__item a:active {
  color: #2ab9db;
}

/* MAIN */

/* SECTION Hello */

.hello {
  position: relative;
  padding: 100px 0;
}

.hello .wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
}

.hello .left-content__title {
  font-size: 72px;
  line-height: 1.2;
  font-weight: 300;
}

.hello .left-content__title strong {
  font-size: 72px;
  color: #56c7e2;
}

.hello .left-content__text {
  display: block;
  font-size: 25px;
  margin-top: 10px;
  font-weight: 300;
}

.hello .left-content__text strong {
  font-size: 25px;
  font-weight: 500;
}

.hello .left-content__btn {
  margin-top: 40px;
}

.hello__img {
  width: 30%;
  height: 30%;
  border-radius: 50%;
}

.hello .fa-chevron-down {
  color: #56c7e2;
  font-size: 24px;
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -12px;
}

.hello .fa-chevron-down:hover,
.hello .fa-chevron-down:focus {
  color: #177288;
}

.hello .fa-chevron-down:active {
  color: #082931;
}

/* SECTION  About */

.about {
  margin-top: 90px;
  text-align: center;
}

.about__text {
  width: 70%;
  margin: 0 auto;
}

.about__stack-title {
  margin-top: 30px;
}

.about__technologies {
  font-weight: 700;
  width: 40%;
  margin: 0 auto;
  text-transform: uppercase;
}

.about .resume {
  margin-top: 30px;
}

.about .resume .fa-file {
  margin-right: 10px;
}

/*SECTION Portfolio*/

.portfolio {
  text-align: center;
  margin-top: 60px;
  padding-bottom: 100px;
}

.portfolio .section-title {
  margin-bottom: 40px;
}

.portfolio .project {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
}

.portfolio .project__item {
  width: calc(33.33333% - 10px);
  border: 4px solid #56c7e2;
  box-sizing: border-box;
  padding: 20px;
  margin-bottom: 20px;
}

.portfolio .project__item:hover a,
.portfolio .project__item:focus a {
  color: #56c7e2;
}

.portfolio .project__item a {
  text-decoration: none;
  color: #535d5e;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.portfolio .project__item span {
  display: block;
  font-size: 22px;
  margin-top: 20px;
}

.portfolio .project__item img {
  display: block;
}

/* FOOTER */

.footer {
  text-align: center;
}

.footer .contact-mail {
  color: #56c7e2;
  text-decoration: none;
  font-size: 22px;
}

.footer .contact-mail:hover,
.footer .contact-mail:focus {
  text-decoration: underline;
}

.footer__logo {
  display: block;
  margin: 30px 0;
}

/* TODO: Social */

.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
}

.social__item {
  margin: 0 15px;
}

.social__item img {
  border: 2px solid #56c7e2;
  padding: 5px;
  border-radius: 25%;
}

.social__item img:hover,
.social__item img:focus {
  transform: scale(1.2);
}

.social__item img:active {
  transform: scale(1.1);
}

@media screen and (max-width: 981px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .section-title {
    font-size: 32px;
  }

  .portfolio .project__item {
    width: calc(50% - 10px);
  }
}

@media screen and (max-width: 748px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .section-title {
    font-size: 20px;
  }

  .header .wrapper {
    flex-direction: column;
  }

  .header-list__item {
    margin: 0 10px;
  }

  .hello .left-content__title {
    font-size: 42px;
  }

  .hello .left-content__title br {
    display: none;
  }

  .hello .left-content__title strong {
    font-size: 32px;
  }

  .hello .left-content__btn {
    display: block;
    text-align: center;
    margin-bottom: 20px;
  }

  .hello .fa-chevron-down {
    display: none;
  }

  .about {
    margin-top: 0;
  }

  .portfolio .project__item {
    width: calc(100% - 10px);
  }

  .footer .contact-mail {
    font-size: 18px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}